.contactSection{
    background-color: #fff;
    height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.contactContent{
    flex-basis: 60%;
    padding: 2rem;
}
.contactContent h2{
    font-weight: 600;
    margin: 0;
    font-size: 1.5rem;
    color: #283148;
    font-family: "Poppins", sans-serif;
}
.contactContent h1{
    font-weight: 900;
    font-size: 2.6rem;
    margin: 0;
    line-height: 1.2;
    font-family: "Poppins", sans-serif;
    color: #283148;
}
.contactContent .timing{
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: -2rem;
}
.contactContent .timing h3{
    font-weight: 600;
    font-size: 1.2rem;
    color: #283148;
    font-family: "Poppins", sans-serif;
}
.contactContent .timing .clockIcon{
    font-size: 2rem;
    color: #283148;
}
.contactContent p{
    color: #283148;
    width: 80%;
    font-size: 1rem;
    margin-bottom: 1.5rem;
}
.contactNumberContainer{
    display: flex;
    gap: 1rem;
    align-items: center;
}
.iconContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #283148;
    width: 4rem;
    height: 4rem;
    border-radius: 1rem;
    margin-right: 2rem;
}
.contactNameAndNo{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 30rem;
}
.contactNumber h3{
    font-size: 1rem;
    color: #283148;
    font-weight: 900;
}
.phoneIcon{
    color: white;
}

.contactFormContainer{
    flex-basis: 40%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.contactFrom{
    background-color: #283148;
    height: 80vh;
    width: 30vw;
    border-radius: 2rem;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    opacity: 0;
    transform: translateX(200px);
    transition: opacity 0.5s ease, transform 0.5s ease;
}
.contactFrom.visible{
    opacity: 1;
    transform: translateX(0);
}
.contactFrom h2{
    text-align: center;
    font-weight: 600;
    margin: 0;
    font-size: 1.1rem;
    line-height: 1.2;
    font-family: "Poppins", sans-serif;
    color: white;
}
.contactForm{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-top: 2rem;
}
.contactFrom .username,
.contactFrom .email,
.contactFrom .phoneno{
    display: flex;
    align-items: center;
    gap: 1rem;
}
.contactFrom .message{
    display: flex;
    align-items: flex-start;
    gap: 1rem;
}
.contactFrom input{
    width: 23vw;
    padding: 0.8rem;
    border-radius: 0.5rem;
    border: none;
    font-size: 1rem;
    font-weight: 900;
    padding-left: 3rem;
}
.contactFrom textarea{
    width: 23vw;
    font-weight: 900;
    font-size: 1rem;
    padding: 0.8rem;
    padding-left: 3rem;
    background-color: white;
}
.contactForm svg{
    margin-right: -3.2rem;
    z-index: 10;
}
.contactForm .message svg{
    margin-top: 1rem;
}
.contactBtn{
    background-color: #FF9B42;
    padding: 1rem;
    border: none;
    color: #283148;
    font-size: 1.1rem;
    font-weight: 900;
    transition: 0.5s;
}
.contactBtn:hover{
    background-color: transparent;
    border: 0.2rem solid #FF9B42;
    color: white;
    cursor: pointer;
}

@media (max-width:1115px){
    .contactSection{
        height: 100vh;
    }
    .contactContent h1{
        font-size: 2rem;
    }
    .contactFrom{
        height: 90vh;
    }
}

@media (max-width:1115px){
    .contactSection{
        height: 100vh;
    }
    .contactContent h1{
        font-size: 2rem;
    }
    .contactFrom{
        height: 90vh;
    }
}

@media (max-width:1075px){
    .contactContent{
        flex-basis: 50%;
    }
    .contactFrom{
        width: 35vw;
        height: 80vh;
    }
    .contactFormContainer{
        flex-basis: 50%;
    }
    .contactFrom input{
        width: 29vw;
        font-size: 0.8rem;
    }
    .contactFrom textarea{
        width: 29vw;
        font-size: 0.8rem;
    }
    .contactBtn{
        font-size: 0.8rem;
    }
    .contactContent h2{
        font-size: 1.2rem;
    }
    .contactContent h1{
        font-size: 2.2rem;
    }
    .contactContent .timing h3{
        font-size: 1rem;
    }
    .contactContent .timing .clockIcon{
        font-size: 2rem;
        color: #283148;
    }
    .contactContent p{
        font-size: 0.9rem;
    }
    .contactNameAndNo{
        width: 25rem;
    }
}

@media (max-width:920px){
    .contactSection{
        height: 160vh;
        flex-direction: column;
    }
    .contactFormContainer{
        padding-bottom: 2rem;
    }
    .contactFrom{
        width: 50vw;
        height: 80vh;
    }
    .contactFormContainer{
        flex-basis: 50%;
    }
    .contactFrom input{
        width: 35vw;
        font-size: 1rem;
    }
    .contactFrom textarea{
        width: 35vw;
        font-size: 1rem;
    }
    .contactBtn{
        font-size: 1rem;
    }
}

@media (max-width:627px){
    .contactSection{
        height: 180vh;
    }
}

@media (max-width:722px){
    .contactFrom{
        width: 70vw;
    }
    .contactFrom input{
        width: 50vw;
        font-size: 1rem;
    }
    .contactFrom textarea{
        width: 50vw;
        font-size: 1rem;
    }
    .contactBtn{
        font-size: 1rem;
    }
}

@media (max-width:555px){
    .contactSection{
        height: 200vh;
    }
    .contactFrom{
        transform: translateX(100px);
    }
    .contactContent{
        padding: 2rem 12rem;
        padding-bottom: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .contactContent h2{
        font-size: 1.2rem;
        text-align: center;
    }
    .contactContent h1{
        font-size: 1.7rem;
        text-align: center;
    }
    .contactContent .timing h3{
        font-size: 0.9rem;
        text-align: center;
    }
    .contactContent .timing .clockIcon{
        font-size: 1.8rem;
        color: #283148;
    }
    .contactContent p{
        font-size: 0.9rem;
        margin-top: 2rem;
        width: 80%;
        text-align: center;
    }
    .contactNameAndNo{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        width: 15rem;
    }
    .contactNumber h3{
        font-size: 1rem;
        color: #283148;
        font-weight: 900;
        margin-top: -1rem;
    }
    .contactNumber h3:first-child{
        margin-top: 0;
    }
}

@media (max-width:515px) {
    .contactFrom{
        width: 80vw;
    }
    .contactFrom input{
        width: 67vw;
    }
    .contactFrom textarea{
        width: 67vw;
    }
}

@media (max-width:451px) {
    .contactSection{
        height: 170vh;
    }
    .contactFrom{
        height: 70vh;
    }
    .contactFrom h2{
        font-size: 0.9rem;
    }
    .contactNumberContainer{
        margin-left: 1.5rem;
        gap: 0;
    }
}

@media (max-width:380px) {
    .contactFrom{
        width: 90vw;
    }
    .contactFrom input{
        width: 73vw;
    }
    .contactFrom textarea{
        width: 73vw;
    }
}